body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

h2, h3, p {
  color: #878787 !important;
  margin: 0px !important;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.weather-block {
  border: solid 1px rgb(223, 225, 229);
  height: 464px;
  margin-left: -20px;
  margin-right: -20px;
  padding: 20px 16px 24px 16px;
  width: 630px;
}

.city-info {
  font-size: 24px;
}
.detailedInfo__details {
  padding-top: 15px;
}

.detailedInfo__details .btn-group {
  padding-top: 15px;
}

.detailedInfo__details .btn-group button {
  background-color:#f6f6f6;
  border: 1px solid #c6c6c6;
  color: #222;
  font-size: 11px;
  font-weight: 700;
  width: 83px;
}

.detailedInfo__details .btn-group button:active {
  /* background-image: -webkit-linear-gradient(top,#f6f6f6,#f1f1f1); */
  /* box-shadow: inset 0 1px 2px rgba(0,0,0,0.1); */
}

.detailedInfo__image {
  float: left;
  margin-top: 15px;
}

.detailedInfo__currentTemperature {
  font-size: 64px;
  padding-left: 10px;
}

.detailedInfo__temperatureBlock {
  float: left;
}

.detailedInfo__units {
  display: block;
  float: right;
  margin-top: 18px;
  vertical-align: top;
}


.singleDayBlock {
  border: 1px solid #ffffff;
  cursor: pointer;
  display: inline-block;
  float: left;
  height: 90px;
  line-height: 1;
  margin: 0;
  text-align: center;
  width: 73px;
}

.active {
  background-color: #fcfcfc;
  border: 1px solid #e9e9e9;
  border-radius: 1px;
}

.singleDayBlock__dayName {
  color: #bababa !important;
  font-size: 13px;
  line-height: 15px;
  padding-top: 7px;
}

.singleDayBlock__image {
  display: inline-block;
}

.singleDayBlock__temp {
  font-weight: normal;
  line-height: 15px;
  font-size: 13px;
}

.singleDayBlock--dayTemp {
  color: #878787 !important;
  padding-right: 5px;
}

.singleDayBlock--nightTemp {
  color: #bababa !important;
}
header {
  display: flex;
  justify-content:center;
}
